.f-content__theme-builder {
    .f-theme-content-wrapper {
        padding: 40px;
        display: flex;
        gap: 40px;

        .builder-sidebar {
            .sidebar-menu {
                display: flex;
                flex-direction: column;
                gap: 20px;

                .menu-item {
                    border: 1px solid #E6D3FF;
                    border-radius: 8px;
                    min-width: 170px;
                    font-family: Inter;
                    font-weight: 500;
                    font-size: 14px;
                    padding: 11.5px;
                    text-align: center;

                    &.active {
                        background-color: #F6EFFE;
                    }
                }
            }
        }

        .builder-content {
            background-color: #FAFAFA;
            border-radius: 30px;
            padding: 30px;
            width: 100%;

            h1 {
                border-bottom: 1px solid #E2E7F6;
                padding-bottom: 30px;
            }

            .control-wrapper {
                display: flex;
                gap: 28px;

                .jkit-control {
                    width: 100%;
                }
            }

            .f-theme-content-save {
                padding: 12px;
                width: 149px;
            }
        }

    }
}